<template>
	<div id="store-house">
		<!--顶部菜单-->
		<nav-bar class="home-nav-bar">
			<div slot="left" class="icon-left" @click="backClick">
				<img src="~assets/images/common/prev2.svg">
			</div>
			<div slot="center">
				货品排行
			</div>
			<div slot="right">
				图标
			</div>
		</nav-bar>
		<!--点击菜单（天数）-->
		<date-btns-nav @profitDateClick="profitDateClick" :text-arr="textArr"/>
		<!--顶部菜单客户页面也用-->
		<store-house-navs :infos="allInfosObject"/>
		<!--数量和金额客户页面也用-->
		<ranking-filter-navs :test="rankingNavsArr" @itemClcik="rankingClcik"/>
		<!--滚动--->
		<scroll class="store-house-scroller"
				ref="scroller"
				:probe-type=3
				@pullingup="loadMove"
		>
		<!--排行列表-->
		<store-house-content :product-arr="proOfmate"/>
		</scroll>
	</div>
</template>

<script>

import NavBar from 'components/common/navbar/NavBar'
import Scroll from 'components/common/scroll/Scroll'
import DateBtnsNav from 'components/common/datebtns/DateBtnsNav'
import RankingFilterNavs from 'components/contents/rankingnavs/RankingFilterNavs'
import StoreHouseNavs from 'components/common/balanceaccountsnavs/StoreHouseNavs'
import StoreHouseContent from './childComps/StoreHouseContent'

import {getGoodsDatas} from 'network/product'


export default {
  name: 'StoreHouse',
	components: {
		NavBar,
		Scroll,
		RankingFilterNavs,
		StoreHouseContent,
		StoreHouseNavs,
		DateBtnsNav
	},
	data(){
		return{
			rankingNavsArr:["","","数量","金额"],
			textArr:['成品仓库','原材料仓库'],
			//货品排行（成品和原料数组-network数组传递）
			productandmaterial:{
				product:{
					page:0,
					list:[
					],
					isMore:true,
				},
				material:{
					page:0,
					list:[
					],
					isMore:true,
				},
			},
			//第二栏显示信息4个信息(类型 金额 库存 和成本)
			storeHouseInfo:{
				procutTypeNum:0,
				procutAllPrice:20,
				procutKucun:0,
				procutChenBen:0,
			},
			currentType:"product",		//当前选中的对象名字
			sortType:'stock',			//当前选中排序类型
			currentDataType:1
			
			
		}
	},
	methods:{
		//返回按钮
		backClick(){
			//this.$router.replace("/customersdetail").catch(res=>{res})
			this.$router.go(-1)
		},
		//加载更多
		loadMove(){
			//防抖
			if(timer) clearTimeout(timer)
			let timer=setTimeout(()=>{
				this.getGoodsDatas(this.currentDataType,"",this.currentType)
				if(this.productandmaterial[this.currentType].isMore){
					this.$refs.scroller.finishPullUp()
				}
				this.$refs.scroller.refresh()
			},50)
			//有问题-条数少了有问题
		},
		//今日 昨日点击按钮
		profitDateClick(index){
			//点击分类
			switch(index){
				case 0:
					this.currentType="product"
					this.currentDataType=1
					break
				case 1:
					this.currentType="material"
					this.currentDataType=0
					break
			}
		},
		//排序-需要抽到工具类
		compare (property){
			return function(a,b){
				let value1=a[property]
				let value2=b[property]
				return value2-value1
			}
		},
		//数量和金额排序按钮
		rankingClcik(index){
			//排序切换
			switch(index){
				case 2:
					this.sortType="stock"
					break
				case 3:
					this.sortType="price"
					break
			}
		},
		//设置价格
		setProductAttr(key,value){
			this.storeHouseInfo[key]=value
		},
		//获取商品
		getGoodsDatas(typeid,productname,type){
			let page=this.productandmaterial[type].page+1;
			getGoodsDatas('allSelect',page,productname,typeid).then(res=>{
				this.productandmaterial[type].list.push(...res)
				this.productandmaterial[type].page+=1
				//如果没有数据了
				if(res.length<=0){
					//不在加载更多
					this.productandmaterial[type].isMore=false
				}
			})
		},
		
	},
	computed:{
		proOfmate(){
			//排序
			let proAndMeatArr=this.productandmaterial[this.currentType].list	
			return proAndMeatArr.sort(this.compare(this.sortType))
		},
		//总价格对象
		allInfosObject(){
			//总价格
			let priceRes=0
			//总库存
			let numRes=0
			//成本价
			let chenBenPrice=0

			
			this.productandmaterial[this.currentType].list.forEach(obj=>{
				chenBenPrice+=parseFloat(obj.costs)*parseInt(obj.stock)		 		//成本总价
				numRes+=parseInt(obj.stock)											//库存
				priceRes+=parseInt(obj.price)*parseInt(obj.stock)				    //销售价
			})
			chenBenPrice=chenBenPrice.toFixed(2)									//截取剩下2位小数
			this.setProductAttr('procutAllPrice',priceRes)
			this.setProductAttr('procutKucun',numRes)
			this.setProductAttr('procutChenBen',chenBenPrice)
			this.setProductAttr('procutTypeNum',this.productandmaterial[this.currentType].list.length)
			
			return this.storeHouseInfo
		}
	},
	mounted(){
		//进来时候排序一次(按数量排)
		//this.productandmaterial[this.currentType].list.sort(this.compare(this.sortType))
		//console.log(this.productandmaterial[this.currentType].list)
		//给storeHouseInfo赋值
		//1.种类
		//2.总金额-暂时只能获取这个其他搭好数据库才能完成
		//设置一个初始值保存总值
		/*this.productandmaterial[this.currentType].list.forEach(key=>{
			this.storeHouseInfo.procutAllPrice+=key.allPrice
		})*/
		//3.库存
		//4.成本
	},
	created(){
		this.getGoodsDatas(1,"",'product')			//商品
		this.getGoodsDatas(0,"",'material')		//原材料
	}
}
</script >

<style spoced>
  .icon-left{
	text-align:left;
	position:relative;
  }
  .icon-left img{
	position:absolute;
	top:10px;
	left:10px;
  }
#store-house{
	background:#f3f3f3;
    height: 100vh;
    position: relative;
	z-index:12;
}

.store-house-scroller{
    /*height:300px;*/
    position: relative;
	height:calc(100% - 44px - 62px - 48px - 40px);
    right: 0;
    left: 0;
	overflow:hidden;
  }
</style>
